export default function jindouyun() {
  const liall = document.querySelectorAll('.nav_item ul li')
  const position = document.querySelector('.position')
  for (let i = 0; i < liall.length; i++) {
    liall[i].onmouseenter = function () {
      anmiton(position, liall[i].offsetLeft)
    }
    liall[i].onmouseleave = function() {
       anmiton(position, 0)
    }
  }
  function anmiton(obj, target, time = 0) {
    //清除以前定时器只保留当前定时器执行(节流)
    clearInterval(obj.timer)
    //缓存动画核心算法目标值-元素现在的偏移量在除以10
      obj.timer = setInterval(function() {
        let step = (target - obj.offsetLeft) / 10
        step = step > 0 ? Math.ceil(step) : Math.floor(step)
        console.log(obj.offsetLeft)
        if(obj.offsetLeft === target) {
          clearInterval(obj.timer)
        }
        obj.style.left = obj.offsetLeft + step + 'px'
      }, time)
  }
}